#login {
    margin-top: 100px;
}

input:not(.select2-search__field), select:not(charge-select):not(facilitator-select) {
    color: black;
    height: 28px;
}

.inputContainer {
    margin-bottom: 20px;
    input {
        margin: 0;
    }

    .btn {
        margin: 0;
    }
}

textarea {
    width: 100%;
    color: black;
}

form {
    position: relative;

    .editOverlay {
        cursor: pointer;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        z-index: 9999;

        //&:hover {
        //    &:after {
        //        content: "\f040";
        //        font-family: FontAwesome;
        //        position:absolute;
        //        top:0;
        //        right: -20px;
        //    }
        //}
    }
}

span.textAreaField {
    display: block;
    position: relative;
    margin-bottom: 20px;
}

.inputField {
    display: inline-block;
    position: relative;
    margin-bottom: 20px;
}

.inputField {
    &.textAreaField{
        display: block;
    }

    input[readonly], select[disabled], textarea[readonly] {
        margin-bottom: 0;
        transition: all 0.2s ease;
        background: $color-main;
        color: $color-white;
        border: none;
    }

    &.showEditIcon {
        input[readonly], select[disabled], textarea[readonly] {
            transition: all 0.2s ease;
            background: $color-seperator;
            cursor: pointer;
            color: black;
        }

        &:after {
            transition: all 0.2s;
            content: "\f040";
            font-family: FontAwesome;
            position:absolute;
            top:0;
            right: -20px;
        }

    }

    &.removeEditIcon {
        &:hover {
            &:after {
                content: "";
            }
        }
    }
}



.charge-select-wrapper, .facilitator-select-wrapper {
    margin-bottom: 20px;
}


select, .select2-dropdown {
    color: black;
}

#edit {

    .showEditIcon .select2-selection.select2-selection--multiple {
        transition: all 0.2s ease;
        background: $color-seperator !important;
        cursor: pointer;
        color: black;

    }

    .select2-container--disabled .select2-selection--multiple {
        transition: all 0.2s ease;
        background: $color-main !important;
        border: none !important;
        cursor: pointer;
        color: black;
    }

    //.select2-selection.select2-selection--multiple {
    //    margin-bottom: 0;
    //    background-color: $color-main !important;
    //    color: $color-white;
    //    border: none;
    //}

    .select2-search.select2-search--inline {
        height: 25px !important;
    }
}

.select2-container {
    width: 300px !important;

    .select2-selection__rendered {
        word-wrap: break-word;
        text-overflow: inherit;
        white-space: normal;
    }
}

.select2-selection__choice {
    color: black;
}
label {
    color: #F5F5F5;
}
label.error {

    .charge-select2-container &, .facilitator-select-container {
        margin-top: 0;
    }

    display: block;
    margin-bottom: 10px;
    text-shadow: 2px 4px 3px rgba(0,0,0,0.3);
    margin-top: -15px;
    color: #ff3232;

    .inputContainer &, .inputField & {
        margin-bottom: 5px;
        margin-top: 5px;
    }
}

.modal {
    color: black;
    margin-left: 16.66666667%;
}

input[type="text"], textarea {
  border-radius: 4px;
}


input[type="text"], select {
  width: 153px;
}